import React from 'react';
import { MapComponent, PointsList, AddPointForm } from '../HOC';
import './MainPage.scss';

const MainPage = () => {
    return (
        <section className='main-page'>
            <div className='main-page__list-wrapper'>
                <AddPointForm />
                <PointsList />
            </div>
            <MapComponent />
        </section>
    );
};

export default MainPage;
