import React from 'react';
import PropTypes from 'prop-types';
import './AddPointForm.scss';


const AddPointForm = React.memo(({ addPoint, centerCoords }) => {
    let pointInput;
    const submit = (e) => {
        e.preventDefault();

        const pointValue = pointInput.value;

        if (pointValue === '') {
            return null;
        }

        const point = {
            point: pointValue,
            centerCoords,
        };

        addPoint(point);

        pointInput.value = '';

        return null;
    };

    return (
        <form
            className='add-point-form'
            method='post'
            onSubmit={submit}
        >
            <input
                className='add-point-form__point-input form-control'
                type='text'
                ref={(input) => {
                    pointInput = input;
                }}
                placeholder='type new point here'
            />
        </form>
    );
});

AddPointForm.defaultProps = {
    addPoint: (f) => { return f; },
    centerCoords: [],
};
AddPointForm.propTypes = {
    addPoint: PropTypes.func,
    centerCoords: PropTypes.arrayOf(PropTypes.number),
};

export default AddPointForm;
